import React, { useState, useEffect } from "react";
import axios from "axios";
import PropTypes from "prop-types";
import logo from "../components/logo 2.png";
import "./login.css";
import Forgetmypass from "./Forgetmypass";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Passwordreset from "./Passwordreset";
import { toast, ToastContainer } from "react-toastify";
const API = "http://192.168.56.1:5000";

export default function Login({ setToken }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [fName, setfName] = useState("");
  const [user, setUser] = useState();
  useEffect(() => {
    const loggedInUser = sessionStorage.getItem("user");
    if (loggedInUser) {
      const foundUser = JSON.parse(loggedInUser);
      setUser(foundUser);
      setfName(foundUser.fName);
      setToken(foundUser.token);
    }
  }, [setToken]);
  const rest = () => {
    toast.error("Wrong email or Wrong password");
    document.getElementById("email").value = "";
    document.getElementById("password").value = "";
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const user = { fName, email, password };
    // send the username and password to the server
    await axios
      .post(`${API}/login`, user)
      .then((resp) => {
        setUser(resp.data);
        setfName(resp.data.fName);
        setToken(resp.data.token);
        // store the user in localStorage
        sessionStorage.setItem("user", JSON.stringify(resp.data));
      })
      .catch((err) => rest());
  };

  // if there's a user show the message below
  if (user) {
  }

  return (
    <>
      <div>
        <ToastContainer />
        <Router>
          <Switch>
            <Route path="/forgetmypass" component={Forgetmypass}></Route>
            <Route path="/resetpassword" component={Passwordreset}></Route>
          </Switch>
        </Router>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-md-6 offset-md-3">
            <div className="card my-5">
              <form
                className="card-body cardbody-color p-lg-5"
                onSubmit={handleSubmit}
              >
                <div className="text-center">
                  <img
                    src={logo}
                    className="img-fluid profile-image-pic img-thumbnail rounded-circle my-3"
                    width="200px"
                    alt="profile"
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="email"
                    className="form-control"
                    id="email"
                    aria-describedby="emailHelp"
                    placeholder="Email"
                    onChange={({ target }) => setEmail(target.value)}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="password"
                    className="form-control"
                    id="password"
                    placeholder="password"
                    onChange={({ target }) => setPassword(target.value)}
                  />
                </div>
                <div className="text-center">
                  <button
                    type="submit"
                    className="btn btn-color px-5 mb-5 w-100"
                  >
                    Login
                  </button>
                </div>

                <a href="/forgetmypass" className="text-dark fw-bold">
                  {" "}
                  Forget Password?
                </a>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
Login.propTypes = {
  setToken: PropTypes.func.isRequired,
};
